body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-footer {
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  font-size: calc(6px + 1vmin);
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  
}

.App-animation-color-cycle {
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-name: colorcycle;
          animation-name: colorcycle;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  text-decoration: none;
}

@-webkit-keyframes colorcycle
{
  0%   {color: red;}
  25%  {color: yellow;}
  50%  {color: rgb(83, 123, 255);}
  75%  {color: rgb(40, 206, 40);}
  100% {color: red;}
}

@keyframes colorcycle
{
  0%   {color: red;}
  25%  {color: yellow;}
  50%  {color: rgb(83, 123, 255);}
  75%  {color: rgb(40, 206, 40);}
  100% {color: red;}
}


.NavBar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding: 2vh;
    padding-left: 30%;
    padding-right: 30%;
    -webkit-justify-content: center;
            justify-content: center;
    color: rgb(63, 63, 63);
    min-height: 5vh;
}

.row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
}
  
.column {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    -webkit-flex: 1 1;
            flex: 1 1;
    color:white;
    height: auto;
}
  
.blue-column {
    background-color: blue;
    height: 100px;
}
  
.green-column {
    background-color: green;
    height: 100px;
}

.NavBar a.NavBar-link:hover, a.NavBar-link:active {
    color: rgb(247, 168, 84);
    text-decoration: none;
}

.NavBar a.NavBar-link:link, a.NavBar-link:visited {
    color: white;
    text-decoration: none;
  }
.About {
    min-height: 40vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(4px + 2vmin);
    color: white;
  }

.InLine {
    display:inline;
  }
  
.About-relocate {
    padding: 10px;
  }


.About-link {
    color: #61dafb;
    text-decoration: none;
  }
  
.Footer {
    min-height: 10vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    font-size: calc(6px + 1vmin);
    color: white;
  }

.Footer-link {
    color: #61dafb;
    text-decoration: none;
  }

.InLine {
    display:inline;
  }

.Footer-mail {
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-name: colorcycle;
            animation-name: colorcycle;
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
    fill: white;
    margin-left: 20px;
    margin-right: 30px;
    display: inline-block;
    vertical-align: middle;
  }

.Footer-social {
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-name: colorcycle;
            animation-name: colorcycle;
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
    text-decoration: none;  
    fill: white;
    padding: 20px;
    margin-bottom: 20px;
}

@-webkit-keyframes colorcycle
{
  0%   {fill: red;}
  25%  {fill: yellow;}
  50%  {fill: rgb(83, 123, 255);}
  75%  {fill: rgb(40, 206, 40);}
  100% {fill: red;}
}

@keyframes colorcycle
{
  0%   {fill: red;}
  25%  {fill: yellow;}
  50%  {fill: rgb(83, 123, 255);}
  75%  {fill: rgb(40, 206, 40);}
  100% {fill: red;}
}
